<template>
  <div id="app" class="container-fluid px-0" v-if="isReady">
    <div
      class="nav flex-x-between w-100"
      :class="{ onScroll: !view.topOfPage }"
    >
      <img
        :src="isDark ? '/dark_logo.png' : '/light_logo.png'"
        width="150px"
        alt=""
      />

      <div class="menu_" style="transform: scale(0.6); z-index: 10000">
        <div
          id="nav-icon3"
          :class="open && 'open'"
          @click="openMenu"
          class="lg-hide"
        >
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
      <slide-y-down-transition>
        <div class="menu flex-align-center flex-column" v-if="open">
          <a href="#home" @click="open = !open">Home</a>
          <a href="#about-us" @click="open = !open">About Us</a>
          <a href="#our-service" @click="open = !open">Our Service</a>
          <a href="#our-work" @click="open = !open">Our Work</a>
          <a href="#pricing" @click="open = !open">Pricing</a>
        </div>
      </slide-y-down-transition>

      <div class="flex-align-center md-hide sm-hide">
        <a href="#home">Home</a>
        <a href="#about-us">About Us</a>
        <a href="#our-service">Our Service</a>
        <a href="#our-work">Our Work</a>
        <a href="#pricing">Pricing</a>
        <button class="button" href="#pricing"><p>Get Started</p></button>
      </div>
    </div>
    <router-view :key="$route.fullPath" class="router_view mx-0 w-100">
    </router-view>

    <slide-y-down-transition>
      <button
        class="no-width scroll-to-top"
        v-if="showScrollToTop"
        @click="scrollToTop"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          stroke-linecap="round"
          stroke-linejoin="round"
          class="feather feather-chevron-up"
        >
          <polyline points="18 15 12 9 6 15"></polyline>
        </svg>
      </button>
    </slide-y-down-transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showScrollToTop: false,
      isDark: false,
      view: {
        topOfPage: true,
      },
      isReady: false,
      open: false,
    };
  },
  computed: {},
  mounted() {
    setTimeout(() => {
      this.fadeLoading();
    }, 2000);
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  watch: {},
  components: {},
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    fadeLoading() {
      const loader = document.querySelector(".page-loader");
      loader.className += " hidden";
      this.isReady = true;
      setTimeout(() => {
        loader.className += " display_none";
      }, 1000);
    },
    openMenu() {
      this.open = !this.open;
    },
    changeTheme(mode) {
      this.isDark = mode;
    },
    handleScroll(e) {
      if (window.pageYOffset > 0) {
        if (this.view.topOfPage) this.view.topOfPage = false;
      } else {
        if (!this.view.topOfPage) this.view.topOfPage = true;
      }
      this.showScrollToTop = e.target.scrollingElement.scrollTop > 500;
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
};
</script>
<style>
.menu a {
  font-size: 20px;
  margin-bottom: 15px;
}
.menu.flex-align-center.flex-column {
  background: rgb(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  position: fixed;
  z-index: 100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  justify-content: center;
}
#nav-icon3 {
  width: 50px;
  height: 45px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon3 span {
  display: block;
  position: absolute;
  height: 5px;
  width: 100%;
  background: rgb(var(--primary));
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

#nav-icon3 span:nth-child(1) {
  top: 0px;
}

#nav-icon3 span:nth-child(2),
#nav-icon3 span:nth-child(3) {
  top: 18px;
}

#nav-icon3 span:nth-child(4) {
  top: 36px;
}

#nav-icon3.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

#nav-icon3.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

#nav-icon3.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#nav-icon3.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

html {
  scroll-behavior: smooth;
}
.bx {
  font-size: 25px !important;
}
.nav {
  background: rgba(0, 0, 0, 0);
  position: fixed !important;
  top: 0px;
  left: 0px;
  padding: 30px 30px;
  width: 100vw !important;
  right: 0px;
  z-index: 100;
  transition: all 0.25s ease-in-out;
}
.nav a {
  text-decoration: none;
  font-weight: 600;
  padding: 0px 30px;
}
.nav.onScroll {
  background: rgba(255, 255, 255);
  padding: 20px 30px;

  box-shadow: 0px 4px 25px -4px rgb(0, 0, 0, 0.15);
}
.scroll-to-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  z-index: 1000000;
}
button.no-width.scroll-to-top:hover {
  background: rgb(var(--primary));
  color: #fff;
}
button.no-width.scroll-to-top svg {
  color: rgb(var(--text));
}
button.no-width.scroll-to-top:hover svg {
  color: #fff;
}
button.no-width.scroll-to-top {
  border: 0px;
  background: #fff;
  border-radius: 100%;
  height: 55px;
  width: 55px;
  box-shadow: 0px 4px 25px -4px rgb(0, 0, 0, 0.25);
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
}

body {
  margin: 0px !important;
}
.flex-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.primary {
  color: #6951a4;
}

p,
h4 {
  margin-top: 0px !important;
  margin-bottom: 10px !important;
}
.flex-start {
  align-items: flex-start !important;
}

.sm-text {
  font-size: 14px !important;
}
.xs-text {
  font-size: 12px !important;
  color: rgb(var(--text), 0.7) !important;
}
.gray {
  color: rgb(var(--text), 0.6);
}
.px-2 {
  margin-right: 10px;
  margin-left: 10px;
}
.pt-3 {
  padding-top: 15px;
}
.mb-3 {
  margin-bottom: 15px;
}
</style>